export  const dataTime = () => {
  const time = new Date();
  const mont = ['' , '' , '' , '' , '' , '' , '' , '' , '' , '' , '' , '']

  const check = (time) => {
   return time < 10 ? `0${time}` : time;
  }
  return `${time.getDate()} ${mont[time.getMonth()]} ${check(time.getHours())}:${check(time.getMinutes())}:${check(time.getSeconds())}`;
}