import {createSlice} from "@reduxjs/toolkit";
import {dataTime} from "../../parsing/parsing";

export type taskCardsType = {
  id: number,
  name: string,
  time: string,
  content: string,
}

const initialState = {
  tasks: [] as Array<taskCardsType>,
};

const Task = createSlice({
  name: 'Task',
  initialState,
  reducers: {
    deleteTask (state, action) {
      state.tasks = state.tasks.filter(el => el.id !== action.payload);
    },
    setNewTask(state, action) {
      const time = new Date();
      const data = {
        id: action.payload.id,
        name: action.payload.name,
        time: dataTime(),
        content: action.payload.content,
      };

      const isExists = state.tasks.findIndex(el => el.id === action.payload.id);

      if(isExists === -1) {
        state.tasks.push(data);
      } else {
        state.tasks[isExists] = data;
      }
    },
  },
});

export const {
  deleteTask,
  setNewTask
} = Task.actions;

export default Task.reducer;