import {useState} from "react";

export const useTouchMoveDelete = (numberMax) => {
  const [change, setChange] = useState(0);
  const [deleteBlock, setDeleteBlock] = useState(false);

  const onSetDeleteBtnSize = (clientX) => {
    if(clientX >= numberMax / 2) {
      setDeleteBlock(true)
    } else {
      setDeleteBlock(false)
    };
  };

  const onTouchStart = (e) => {
    e.currentTarget.style.transition = '0s';
    setChange(e.changedTouches[0].clientX - e.currentTarget.getBoundingClientRect().left);
  };
  const onTouchMove = (e) => {
    if(change - e.changedTouches[0].clientX <= numberMax + 50) {
      closeDeleteBtn(e, change - e.changedTouches[0].clientX);
    };
    onSetDeleteBtnSize(change - e.changedTouches[0].clientX);
  };
  const onTouchEnd = (e) => {
    e.currentTarget.style.transition = '0.3s';
    const widthDifference = change - e.changedTouches[0].clientX;
    const right = widthDifference >= numberMax ? numberMax : 0;
    e.currentTarget.style.transform = `translateX(-${right.toFixed(1)}px)`;
    if(right === 0) {
      setTimeout(() => setDeleteBlock(false), 100)
    }
  };
  const onDragStart = (e) => {
    e.currentTarget.style.transition = '0s';
    setChange(e.clientX - e.currentTarget.getBoundingClientRect().left);
  };
  const onMouseDown = (e) => {
    e.currentTarget.style.transition = '0s';
    setChange(e.clientX - e.currentTarget.getBoundingClientRect().left);
  };
  const onDrag = (e) => {
    if(change - e.clientX <= numberMax + 50) {
        closeDeleteBtn(e, change - e.clientX);
      };
      onSetDeleteBtnSize(change - e.clientX);
  }
  const onDragEnd = (e) => {
    e.currentTarget.style.transition = '0.3s';
      const widthDifference = change - e.clientX;
      const right = widthDifference >= numberMax ? numberMax : 0;
      e.currentTarget.style.transform = `translateX(-${right.toFixed(1)}px)`;
      if(right === 0) {
        setTimeout(() => setDeleteBlock(false), 100)
      }
  };
  const onMouseUp = e => {
    e.currentTarget.style.transition = '0.3s';
    const widthDifference = change - e.clientX;
    const right = widthDifference >= numberMax ? numberMax : 0;
    e.currentTarget.style.transform = `translateX(-${right.toFixed(1)}px)`;
    if(right === 0) {
      setTimeout(() => setDeleteBlock(false), 100)
    }
  }


  return {
    move: {
      onTouchStart,
      onTouchMove,
      onTouchEnd,
      onDragStart,
      onDragEnd,
      onDrag,
      onMouseUp,
      onMouseDown,
    },
    deleteBlock
  };
};

const closeDeleteBtn = (e, right = 0) => {
  e.currentTarget.style.transform = `translateX(-${right.toFixed(1)}px)`
};

















